html,
body {
  overflow:hidden;
  width: 100%;
  height: 100%;
  /* max-width: 1366px; */
  /* max-height: 1024px; */
  margin: auto;
  border: 1px solid var(--primary);
}

.prev, .next {
  color: #2282ff;
  cursor: pointer;
}

.sidenav {
  margin-top: 268px; 
  height: 40%;
  font-size: 20px; 
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: rgb(46, 109, 255);
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 50px;
  width: 0px;
  color:white; 
  font-size: 60%
}

.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 17px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.sidenav a:hover {
  color: #f1f1f1;
}

.sidenav .closebtn {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0px;
  font-size: 30px;
  color: white; 
}

.float-container {
  border: 3px solid #fff;
  padding: 20px;
  overflow: scroll;
  display: flex; 
  width: 100%; 
}

.float-child {
  flex: 1; 
  float: left;
  padding: 20px;
} 

.float-child-left {
  width: 20%;
  float: left;
  padding: 20px;
  font-size: 15px; 
  cursor: pointer; 
  color: black;
  border: #2282ff;
  border-right: #2282ff;
  border-right-width: 1px;
  border-right-style:solid;
} 

@media screen and (max-height: 450px) {
  .sidenav {padding-top: 15px;}
  .sidenav a {font-size: 18px;}
}

#hamburger {
  align-items: left !important; 
  padding-left: 20px; 
  padding-top: 20px; 
  cursor: pointer; 
  color: #2282ff
}

.hamburger {
  align-items: left !important; 
}

.arrows {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: auto;
  margin-bottom: 20px; 
}

.card-title { 
  color: #2282ff;
}

.prev:hover {
  color: black;
}
.next:hover {
  color: black;
}

.arrow { 
  display: flex;
  justify-content: space-between;
}

.outline-container { 
  background-color: rgb(235, 233, 233); 
  max-width: 1080px;
  height: 790px;
  max-height: 790px;
  margin: auto;
  text-align: center;
  font-family: "Gill Sans", sans-serif;
  font-size: 22px;
}

.card { 
  height: 550px;
  max-width: 80%;
  margin: auto;
  margin-top: 10px;
  text-align: left;
}

.btn { 
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px; 
  width: 50px;
  height: 50px;
  font-size: 20px;
  text-decoration: none;
  color: white; 
  background-color:  rgb(35, 130, 255);
}

.nav-btns { 
  
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px; 
  width: 125px;
  height: 40px;
  font-size: 20px;
  text-decoration: none;
  color: white; 
  background-color:  rgb(35, 130, 255);
  vertical-align: top;
  border-style: solid;
  margin-top: 10px;
  box-shadow: grey;
}

.btn:hover { 
  background-color: rgb(127, 181, 251);
}

a{
  color: white; 
  text-decoration: none;
}

a:hover{
  color: white; 
  text-decoration: none;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 8px;
}

::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(0,0,0,.5);
    box-shadow: 0 0 1px rgba(255,255,255,.5);
}

#root {
  width: 100%;
  height: 100%;
  overflow-y: hidden;
}

.container,
.main {
  width: 100%;
  margin: 0 10px;
  /* height: calc(100% - 74px); */
  /* overflow: hidden; */
}

.row {
  height: 100%;
  width: 100%;
  /* overflow: hidden; */
}

.col {
  height: 100%;
  padding: 15px;
  /* overflow-y: scroll; */
  /* -webkit-overflow-scrolling: touch; */
}

ul {
  list-style: none;
  padding-left: 20px;
  margin: 0px;
}
ul.first {
  padding-left: 5px;
}

.wrapper {
  width: 70vw;
  height: 85vh;
  margin: auto;
}

.dev {
  border: 1px dashed hotpink;
}

.empty-heading {
  padding-left: 13px;
}

.checkbox-icon {
  /* margin-top: 5px; */
  transform: translateY(5px);
}

.callout-icon {
  margin-left: 10px;
}

.callout {
  margin-left: 10px;
  /* color: blue; */
  font-style: italic;
  overflow: none;
  text-overflow: ellipsis;
}
.callout.currentCallOut {
  background-color: yellow;
}
.card-body {
  padding-top: 5px;
  overflow: scroll;
}




.slides { 
  padding-top: 35px; 
  padding-right: 55px; 
  padding-left: 55px; 
  overflow: scroll; 
}

.intervention-form {
  margin-left: 20px;
}

.hidden {
  display: none;
}

th.table-header {
  position: sticky;
  top: 0;
  z-index: 2;
  background-color: blue;
  color: white;
}

tbody.scrolling {
  overflow-y: auto;
}

.invisible { 
  visibility: hidden;
}

a:disabled { 
  pointer-events: none;
  color: pink;
}

.slide { 
  margin-bottom: 20px; 
}

.btn-disabled {
  cursor: none;
  pointer-events:none;
  color: white;
  background-color: #c0c0c0;
  border-color: grey;
}

.btn-disabled:hover { 
  cursor: none;
  pointer-events:none;
  background-color: #c0c0c0;
}

.currPhase {
  border: 1px solid black; 
  background-color: black;
}

.btn:active:focus { 
  border: 1px solid black; 
}

.link-disabled, .btn-disabled:hover {
  cursor: not-allowed;
  cursor: none;
  pointer-events:none;
  color: #c0c0c0;
}

.pageCount{
  text-align: right; 
}

/* li > a.currentPhase{
  border: 1px solid red;
} */

.phaseTabs{ 
  display: inline;
}

